.content {
    height: 100vh;
    overflow: hidden;
  }
  .list-friends {
    overflow: auto !important;
    height: 65vh;
  }
  .list-chats {
    overflow: auto !important;
    height: 70vh;
    padding: 10px;
  }
  .friend-chat {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px,
      rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    margin-top: 3vh;
    margin-right: 2vh;
  }
  
.header-sender {
    padding: 3px;
    position: relative;
    width: 100%;
    margin-top: 10px;
  }
  .sub-sender {
    display: flex;
    justify-content: flex-end;
  }
  .contend-msg {
    background-color: white;
    padding: 0 25px;
    border-radius: 35px;
    border-bottom-right-radius: 10px;
    position: relative;
    margin-left: 3px;
    max-width: 200px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  }
  
  .deletead {
    position: absolute;
    top: 0;
    left: 0;
    /* right: 10; */
    cursor: pointer;
    /* display: block; */
    color: black;
  }
  .deletead:hover {
    color: red;
    transition: top 0.5s ease;
  }
  .info{
    color: rgb(0, 0, 0);
    
  }
  .msg {
    color: rgb(0, 0, 0);
    position: relative;
    top: 6px;
  }
  .msgreceiv {
    color: white;
    position: relative;
    top: 6px;
  }
  .img-msg {
    position: relative;
    width: 100px;
  }
  .images-msg {
    margin-left: 0.75rem;
    height: 4rem;
    border-radius: 9999px;
    width: 56px;
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 0;
    object-fit: cover;
  }
  .deltes {
    color: black;
  }
  .deltes:hover {
    color: red;
  }
  .date-msg {
    display: flex;
    justify-content: flex-end;
    margin-right: 80px;
  }
  .dates-msg {
    color: gray;
    margin-top: 3px;
    margin-right: 27px;
  }
  .header-receiver {
    padding: 3px;
    position: relative;
    width: 100%;
    display: flex;
    margin-top: 7px;
  }
  .sub-receiver {
    position: relative;
    width: 60px;
    flex-direction: column;
  }
  .date-receiver {
    display: flex;
    justify-content: flex-start;
    margin-left: 12px;
    margin-bottom: 5px;
    flex-direction: column;
  }
  .dater-msg {
    margin-left: 75px;
    color: gray;
    margin-top: 5px;
  }
  .img-receiver {
    margin-left: 0.75rem;
    height: 4rem;
    border-radius: 40%;
    width: 56px;
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 0;
    object-fit: cover;
  }
  .msg-receiver {
    background: #7E98DF;
    padding: 7px 18px;
    color: black;
