.header-sender {
    padding: 3px;
    position: relative;
    width: 100%;
    margin-top: 10px;
  }
  .sub-sender {
    display: flex;
    justify-content: flex-end;
  }
  .contend-msg {
    background-color: white;
    padding: 0 25px;
    border-radius: 35px;
    border-bottom-right-radius: 10px;
    position: relative;
    margin-left: 3px;
    max-width: 200px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  }
  
  .deletead {
    position: absolute;
    top: 0;
    left: 0;
    /* right: 10; */
    cursor: pointer;
    /* display: block; */
    color: black;
  }
  .deletead:hover {
    color: red;
    transition: top 0.5s ease;
  }
  .info{
    color: rgb(0, 0, 0);
    
  }
  .msg {
    color: rgb(0, 0, 0);
    position: relative;
    top: 6px;
  }
  .msgreceiv {
    color: white;
    position: relative;
    top: 6px;
  }
  .img-msg {
    position: relative;
    width: 100px;
  }
  .images-msg {
    margin-left: 0.75rem;
    height: 4rem;
    border-radius: 9999px;
    width: 56px;
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 0;
    object-fit: cover;
  }
  .deltes {
    color: black;
  }
  .deltes:hover {
    color: red;
  }
  .date-msg {
    display: flex;
    justify-content: flex-end;
    margin-right: 80px;
  }
  .dates-msg {
    color: gray;
    margin-top: 3px;
    margin-right: 27px;
  }
  .header-receiver {
    padding: 3px;
    position: relative;
    width: 100%;
    display: flex;
    margin-top: 7px;
  }
  .sub-receiver {
    position: relative;
    width: 60px;
    flex-direction: column;
  }
  .date-receiver {
    display: flex;
    justify-content: flex-start;
    margin-left: 12px;
    margin-bottom: 5px;
    flex-direction: column;
  }
  .dater-msg {
    margin-left: 75px;
    color: gray;
    margin-top: 5px;
  }
  .img-receiver {
    margin-left: 0.75rem;
    height: 4rem;
    border-radius: 40%;
    width: 56px;
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 0;
    object-fit: cover;
  }
  .msg-receiver {
    background: #7E98DF;
    padding: 7px 18px;
    color: black;
    border-radius: 35px;
    position: relative;
    margin-left: 20px;
    max-width: 200px;
    border-bottom-left-radius: 10px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  }
  